@import "../../inc/vars";
.acro-brands {
	background: linear-gradient(265deg, rgba(241, 237, 250, 0.63) 7.21%, #F3EEFF 85.09%);
	padding: 60px 15px;
	&__title {
		color: #5A6272;
		text-align: center;
		@include pop(400, 23, 1.36);
		letter-spacing: -0.46px;
		margin-bottom: 40px;
		@media (min-width: $screen-sm){
			@include pop(400, 29, 1.24);
			letter-spacing: -0.58px;
		}
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		gap: 26px;
		.list-item {
			padding: 20px;
		}
		@media (min-width: $screen-sm){
			gap: 80px;
			.list-item {
				padding: 0;
			}
		}
	}
}